@import url(https://fonts.googleapis.com/css2?family=ABeeZee&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  position: relative;
  height: 100vh;
}

h1{
  font-size: 22px;
  font-weight: normal;
  line-height: 33px;
}

h4 {
  font-size: 16px;
  line-height: 26px;
}

h1, h4, .menu-item {
  text-align: center;
  font-family: 'ABeeZee', sans-serif;
}

h2 {
  font-style: italic;
  font-family: 'ABeeZee', sans-serif;
}

.menu-item {
  line-height: 20px;
}

.App-logo {
  height: 80px;
  pointer-events: none;
}

#root, .app {
  height: 100%;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #797a7c;
  height: 150px;
  display: flex;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#test-id {
    height: 100vh;
    width: 100vw;
    background-image: url(/Baseballscorekeeper/static/media/bg1.1fa6b3e3.png);
    background-color: #202020;
    background-repeat: no-repeat;
    background-size: cover;
}

#Title {
    color: White;
    position: absolute;
    right: 10%;
    bottom: 30%;
}

#Title h1 {
    font-size: 48px;
}
#about {
    background: rgb(38,38,38);
    background: linear-gradient(60deg, rgba(38,38,38,1) 0%, rgba(74,74,74,1) 25%, rgba(125,125,125,1) 76%, rgba(204,204,204,1) 100%);
    height: 100vh;
    width: 100vw;
    color: white;
}
#about-container {
    margin-left: 5%;
}


#about-container h1 {
    position: absolute;
    top: 10%;
    left: 25%;
    font-size: 52px;
}

#about-container h2 {
    position: absolute;
    top: 25%;
    left: 30%;
    font-size: 28px;
}
